<template>
  <div class="error404">
    <div class="error404-body-con">
      <el-card>
        <div class="error404-body-con-title">
          4
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-iosnavigateoutline"></use>
            </svg>
          </span>
          4
        </div>
        <p class="error404-body-con-message">YOU&nbsp;&nbsp;LOOK&nbsp;&nbsp;LOST</p>
        <div class="error404-btn-con">
          <el-button @click="goHome" size="large" style="width: 200px;" type="text">前往首页</el-button>
          <el-button @click="backPage" size="large" style="width: 200px;margin-left: 40px;" type="primary">返回上一页
          </el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Error404',
    methods: {
      backPage () {
        this.$router.go(-1)
      },
      goHome () {
        this.$router.push({
          name: 'MyCourse'
        })
      }
    }
  }
</script>

<style scoped>
  @keyframes error404animation {
    0% {
      transform: rotateZ(0deg);
    }
    20% {
      transform: rotateZ(-60deg);
    }
    40% {
      transform: rotateZ(-10deg);
    }
    60% {
      transform: rotateZ(50deg);
    }
    80% {
      transform: rotateZ(-20deg);
    }
    100% {
      transform: rotateZ(0deg);
    }
  }

  .error404-body-con {
    width: 700px;
    height: 500px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

  }

  .error404-body-con-title {
    text-align: center;
    font-size: 240px;
    font-weight: 700;
    color: #2d8cf0;
    height: 260px;
    line-height: 260px;
    margin-top: 40px;
  }

  .error404-body-con-title span {
    display: inline-block;
    color: #19be6b;
    font-size: 240px;
    animation: error404animation 3s ease 0s infinite alternate;
    margin: 0 -70px;
  }

  .error404-body-con-message {
    display: block;
    text-align: center;
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 12px;
    color: #dddde2;
  }

  .error404-btn-con {
    text-align: center;
    padding: 20px 0;
    margin-bottom: 40px;
  }
</style>



